










































































import Vue from "vue";
import Recette from "../models/recette";
export default Vue.extend({
  data: () => {
    return {
      loading: false,
      id: "" as string,
      recipe: null as Recette | null,
      noImgUrl: "http://www.djerba-troc.com/wp-content/uploads/no-image.png",
      comment: "",
      dialog: false,
      selectedIngredient: "",
      loadProducts: false,
      productList: [] as Array<any>
    };
  },

  computed: {
    paramsId(): string {
      return this.$route.params.id;
    }
  },
  mounted() {
    this.id = this.paramsId;
    this.getRecipe();
  },
  methods: {
    async getRecipe() {
      this.loading = true;
      await fetch(`${process.env.VUE_APP_BACKEND_ENDPOINT}/getRecipe`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          id: this.id
        })
      })
        .then(response => {
          return response.json();
        })
        .then(json => {
          this.loading = false;
          this.recipe = json;
        });

      this.loading = false;
    },
    async postComment() {
      if (!this.recipe) return;
      const response = await fetch(
        "https://web-server-client.herokuapp.com/addComments",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            id: this.recipe._id,
            comment: this.comment
          })
        }
      );
      const content = await response.json();
      this.getRecipe();
      this.comment = "";
    },
    openDialogWithIngredients(ingredient: string) {
      this.selectedIngredient = ingredient;
      this.dialog = true;
      this.getProductWithIngredient(ingredient.toLowerCase());
    },
    getProductWithIngredient(ingredient: string) {
      this.loadProducts = true;
      fetch("https://web-server-client.herokuapp.com/ingredients", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          ingredients: [ingredient],
          additives_tags: []
        })
      })
        .then((res: any) => {
          return res.json();
        })
        .then(json => {
          this.productList = [];
          json.forEach((el: any) => {
            this.productList.push({ name: el.product_name, id: el.id });
          });
          this.loadProducts = false;
        });
    },
    goToProductDetails(id: string) {
      this.dialog = false;
      this.$router.push({ path: `/home/details/${id}` });
    }
  },
  watch: {
    paramsId(val, oldval) {
      if (val === oldval) return;

      this.id = this.paramsId;
      this.recipe = null;
      this.getRecipe();
    }
  }
});
