























import Vue from "vue";
export default Vue.extend({
  props: {
    filterByScore: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      search: null as string | null,
      scoreToShow: 0
    };
  },
  methods: {
    onUserSearch(val: string) {
      this.$emit("filterListByText", val);
    }
  },
  watch: {
    scoreToShow(val: string) {
      this.$emit("filterListByScore", val);
    }
  }
});
